<template>
  <div>
    <allocation-sidebar
      :isSidebarActive="allocationSidebar"
      @closeSidebar="toggleDataSidebar"
      :data="sidebarData"
      @update="updateGrid"
      @add="addGrid"
    />
    <vx-card ref="MainCard">
      <div>
        <vs-table
          ref="gridTable"
          v-model="selected"
          pagination
          :max-items="itemsPerPage"
          search
          :data="allocations"
          class="vs-con-loading__container"
        >
          <div slot="header" class="flex flex-wrap-reverse items-center flex-grow">
            <!-- ADD NEW -->
            <vs-button color="warning" type="gradient" @click="GetGrid()"        icon-pack="feather" icon="icon-refresh-ccw">Reload</vs-button>
            <vs-button color="success" type="gradient" @click="addDataSidebar"  icon-pack="feather" icon="icon-plus-square">Add New</vs-button>
            <!-- ITEMS PER PAGE -->
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-4">
              <div
                class="p-4 border border-solid border-grey-light rounded-full d-theme-dark-bg cursor-pointer flex items-center justify-between font-medium"
              >
                <span
                  class="mr-2"
                >{{ currentPage * itemsPerPage - (itemsPerPage - 1) }} - {{ allocations.length - currentPage * itemsPerPage > 0 ? currentPage * itemsPerPage : allocations.length }} of {{ allocations.length }}</span>
                <feather-icon icon="ChevronDownIcon" svgClasses="h-4 w-4" />
              </div>
              <!-- <vs-button class="btn-drop" type="line" color="primary" icon-pack="feather" icon="icon-chevron-down"></vs-button> -->
              <vs-dropdown-menu>
                <vs-dropdown-item @click="itemsPerPage=10">
                  <span>10</span>
                </vs-dropdown-item>
                <vs-dropdown-item @click="itemsPerPage=20">
                  <span>20</span>
                </vs-dropdown-item>
                <vs-dropdown-item @click="itemsPerPage=50">
                  <span>50</span>
                </vs-dropdown-item>
                <vs-dropdown-item @click="itemsPerPage=100">
                  <span>100</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
          <template slot="thead">          
            <vs-th sort-key="Allocation">Allocation</vs-th>
            <vs-th sort-key="Description">Description</vs-th> 
             <vs-th sort-key="Group">Group</vs-th>           
            <vs-th>Action</vs-th>
          </template>
          <!-- visible: false -->
          <template slot-scope="{data}">
            <vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data">
              <vs-td :data="tr.allocation">{{tr.allocation}}</vs-td>
              <vs-td :data="tr.description">{{tr.description}}</vs-td>
                <vs-td :data="tr.allocationGroupName">{{tr.allocationGroupName}}</vs-td>
              <vs-td class="whitespace-no-wrap">
                <feather-icon
                  icon="EditIcon"
                  svgClasses="w-5 h-5 hover:text-primary stroke-current"
                  @click.stop="editDataSidebar(tr)"
                />
                <feather-icon
                  icon="TrashIcon"
                  svgClasses="w-5 h-5 hover:text-danger stroke-current"
                  class="ml-2"
                  @click.stop="deleteGrid(tr)"
                />
              </vs-td>
            <template class="expand-user" slot="expand" >
                <div class="con-expand-users w-full" >
                    <div>
                    <vs-table
                          ref="gridTable2"
                          v-model="selected"
                          :data="tr.perStore"                           
                        >
                          <template slot="thead">
                            <vs-th>Remove</vs-th>
                            <vs-th>Store</vs-th> 
                          </template>
                          <!-- visible: false -->
                          <template slot-scope="{data}">
                            <vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data">
                            
                              <vs-td class="whitespace-no-wrap">
                                <feather-icon
                                  icon="TrashIcon"
                                  svgClasses="w-5 h-5 hover:text-danger stroke-current"
                                  class="ml-2"
                                  @click.stop="deletePerStoreAllocation(tr)"
                                />
                              </vs-td>
                              <vs-td :data="tr.storeName">{{tr.storeName}}</vs-td>
                            </vs-tr>
                          </template>
                        </vs-table>
                    </div>
                </div>
            </template>
            </vs-tr>
          </template>
        </vs-table>
      </div>
    </vx-card>
  </div>
</template>

<script>
import axios from "axios";
import flatPickr from "vue-flatpickr-component";
import "flatpickr/dist/flatpickr.css";
// import productSidebar from "../saleshunter/ProductSidebar.vue";
import allocationSidebar from "./AllocationSidebar.vue";
export default {
  data() {
    return {
      itemsPerPage: 10,
      allocationSidebar: false,
      selected: [],
      allocations: [],
      sidebarData: {},
      isMounted: false,
 
    };
  },
  components: {
    flatPickr,
    allocationSidebar
  },
  computed: {
    currentPage() {
      if (this.isMounted) {
        return this.$refs.gridTable.currentx;
      }
      return 0;
    }
  },
  
  mounted() {
    this.GetGrid();
    this.isMounted = true;
  },
  methods: {
   
   successNotify() {
      this.$vs.notify({
        title: 'Successfully',
        text: 'Item successfully added | updated | removed',
        color: 'success'
      })
    },
  FailedNotify() {
      this.$vs.notify({
        title: 'Not Successfull',
        text: 'Item not added',
        color: 'danger'
      })
    },

    showLoadingOnElement(element, scale, type) {
      var self = this;
      self.$vs.loading({
        type: type,
        container: self.$refs[element].$el,
        scale: scale
      });
    },
    addDataSidebar() {
      this.sidebarData = {};
      this.toggleDataSidebar(true);
    },
    editDataSidebar(data) {
      data.accountId = 12
      this.sidebarData = data;
      this.toggleDataSidebar(true);
    },

    //********************* SQL events */
    addGrid(data) {   
      const self = this;
      data.accountId = 12 ;// JSON.stringify(data),
      self.showLoadingOnElement("gridTable", 1, "radius");
      var onSuccess = function(response) {
        self.GetGrid();
      };
      var onFinally = function() {
        self.$vs.loading.close(self.$refs.gridTable.$el);
        self.successNotify();
      };
       self.$ajaxPost(
        self,
         "Allocations",
        data,
        onSuccess,
        onFinally
      );
    },
     updateGrid(data) {
       const self = this;
      
      self.showLoadingOnElement("gridTable", 1, "radius");
      var onSuccess = function(response) {
        self.GetGrid();
      };
      var onFinally = function() {
        self.$vs.loading.close(self.$refs.gridTable.$el);
         self.successNotify();
      };
      self.$ajaxPut(
        self,
        "Allocations/" + data.id,
        data,
        onSuccess,
        onFinally
      );
    },
    deletePerStoreAllocation(data) {
      debugger;
      const self = this;
      self.showLoadingOnElement("gridTable", 1, "radius");
      
      var onSuccess = function(response) {
        self.GetGrid();
      };
      var onFinally = function() {
        self.$vs.loading.close(self.$refs.gridTable.$el);
         self.successNotify();
      };
      self.$ajaxDelete(
        self,
        "PerAllocationStores/" + data.perstoreID + "?AccountId=12" ,
        onSuccess,
        onFinally
      );
    },
       deleteGrid(data) {
      const self = this;
      self.showLoadingOnElement("gridTable", 1, "radius");
      
      var onSuccess = function(response) {
        self.GetGrid();
      };
      var onFinally = function() {
        self.$vs.loading.close(self.$refs.gridTable.$el);
         self.successNotify();
      };
      self.$ajaxDelete(
        self,
        "Allocations/" + data.id + "?AccountId=" +  data.accountId,
        onSuccess,
        onFinally
      );
    },
    GetGrid() {
      const self = this;
      self.showLoadingOnElement("gridTable", 1, "radius");
      var onSuccess = function(response) {
        self.allocations = response.data;
      };
      var onFinally = function() {
        self.$vs.loading.close(self.$refs.gridTable.$el);
      };
      self.$ajaxGet(
        self,
         "Allocations/12",
        onSuccess,
        onFinally
      );
    },
    toggleDataSidebar(val = false) {
      this.allocationSidebar = val;
    },
  }
};

